import { IconButton, InputAdornment, TextField } from '@mui/material';
import { ErrorMessage, useField } from 'formik'
import React from 'react'
import { theme } from '../../configtheme';
import { styled } from '@mui/material/styles';
import { GridSearchIcon } from '@mui/x-data-grid';


const CustomTextField = styled(TextField)`
    & label span{
        color:red!important
    }
    & input:disabled{
        color:red!important;
        -webkit-text-fill-color: rgba(0, 0, 0, 1.0);
    }
    
`

export const InputBr = ({ label,handleBr,btn_disabled, ...props }) => {
    
    const [field, meta] = useField(props);
    return (
        <>  <CustomTextField
            margin="normal"
            fullWidth
            label={label}
            {...props}
            {...field}
            error={(meta.error !== undefined && meta.touched)}
            InputProps={
                {
                    endAdornment:
                        <IconButton
                            disabled={btn_disabled}
                            aria-label="toggle password visibility"
                            onClick={handleBr}
                            edge="end"
                        >
                            <GridSearchIcon />
                        </IconButton>
                }
            }
        />
            <ErrorMessage name={props.name} component="span" style={{ color: theme.palette.valen.error, fontSize: '12px' }} />
        </>

    )
}
