import { Button, Divider, Grid, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { uiCleanQuickSearch, uiErrorMessage, uiSetQuickSearchId, uiShowQuickSearch } from '../../redux/actions/ui.actions';
import PaidIcon from '@mui/icons-material/Paid';
import { InputBr } from '../../components/inputs/InputBr';
import { Loading } from '../../components/Loadings/Loading';
import ErrorAlert from '../../components/Alerts/ErrorAlert';
import { SearchModal } from '../../components/QuickSearch/SearchModal';
import { ClientesColumn } from '../../components/QuickSearch/QsColumns';
import { Form, Formik } from 'formik';
import { NormalButton } from '../../components/buttons/NormalButton';
import { GetPrestamosByCliente, InitPrestamo } from '../../redux/actions/prestamo.actions';
import { formInit } from '../../redux/actions/form.actions';
import { CardPrestamo } from '../../components/Cards/CardPrestamo';
import { ModalPagar } from '../../components/Modal/ModalPagar';

const initialValues = {
    id_cliente: '',
    cliente: ''
}

const Pagos = () => {
    const dispatch = useDispatch();
    const { quickSearchSelected, quickSearchId } = useSelector(state => state.ui);
    const { lista_prestamos } = useSelector(state => state.prestamo)
    const [formValues, setFormValues] = useState(null);

    useEffect(() => {
        if (quickSearchSelected) {
            if (quickSearchId === 'cliente') return loadData();
        }
        setFormValues(null);
    }, [quickSearchSelected])

    useEffect(() => {
        dispatch(formInit());
        dispatch(uiCleanQuickSearch());
        dispatch(InitPrestamo());
    }, [dispatch])

    const loadData = () => {
        setFormValues({ ...initialValues, id_cliente: quickSearchSelected._id, cliente: quickSearchSelected.nombre + " " + quickSearchSelected.apellido })
        dispatch(InitPrestamo());
    }

    const handleSearch = () => {
        dispatch(uiSetQuickSearchId('cliente'))
        dispatch(uiShowQuickSearch());
    }

    const handleConsultar = () => {
        if(!quickSearchSelected) return dispatch(uiErrorMessage('Porfavor Seleccione un cliente')); 
        dispatch(GetPrestamosByCliente(quickSearchSelected._id))
    }

    return (
        <>
            <Formik
                initialValues={formValues || initialValues}
                onSubmit={(values) => {
                }}
                validationSchema={{}}
                enableReinitialize
            >
                {
                    ({ handleReset, isValid, errors, resetForm }) => (
                        <Form>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <Typography variant="h6" gutterBottom>
                                    Pagos
                                </Typography>
                                <PaidIcon />
                            </div>
                            <Divider />
                            <Grid container spacing={1}>
                                <Grid item xs={9} md={6}>
                                    <InputBr
                                        handleBr={handleSearch}
                                        disabled={true}
                                        margin="dense"
                                        fullWidth
                                        id="cliente"
                                        label="Cliente"
                                        name="cliente"
                                    />
                                </Grid>
                                <Grid item xs={3} md={2}>
                                    <NormalButton text="Buscar" onClick={handleConsultar} />
                                </Grid>

                                <Grid item xs={12} md={12}>
                                    <CardPrestamo />
                                </Grid>

                            </Grid>

                            {/* <FloatButton handleSave={handleSave} handleSearch={handleSearch} handleDelete={handleDelete} /> */}
                        </Form>
                    )
                }
            </Formik>
            <Loading />
            <ErrorAlert />
            {
                quickSearchId === 'cliente' && <SearchModal title="busqueda de clientes" api="clientes/getCliente" columns={ClientesColumn} />
            }
            
        </>
    )
}

export default Pagos